<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>广告列表</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative">
          <el-autocomplete
          class="inline-input"
          v-model="state2"
          :fetch-suggestions="querySearch"
          placeholder="请输入内容"
          :trigger-on-focus="false"
          @select="handleSelect"
        ></el-autocomplete>
        <div class="from-group">
        </div>
        </form>
      </div>
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            align='center'
            label="图片">
            <template slot-scope="scope">
              <img v-if="!!scope.row.img" :src="scope.row.img" width="150" height="100">
              <template v-else>无</template>
            </template>
          </el-table-column>
          <el-table-column
            prop="title"
            label="标题"
            width="180">
          </el-table-column>
          <el-table-column
            prop="url"
            label="跳转地址"
            width="180">
          </el-table-column>
          <el-table-column
          align='center'
          label="状态">
          </el-table-column>
          <el-table-column
            align='center'
            label="操作">
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      // 分页数据
      currentPage: 1,
      pageSizes: [10],
      pagesize: 10,
      selectData: [],
      total: 0,
      tableData: [],
      addManagerVisible: false,
      state: 1,
      sureLoading: false,
      imageUrl: '',
      addTableData: {
        url: '',
        state: 1,
        imageUrl: '',
        aid: ''
      },
      forid: '',
      restaurants: []
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    querySearch (queryString, cb) {
      let d = {
        tag: 'member',
        op: 'getuserbyphone',
        condition: {
          phone: this.state2
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          var restaurants = da.data.map(user => {
            let {phone, name, num1} = user
            let value = user.phone
            return {value}
          })
          var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
          cb(results)
        } else {
        }
      })
    },
    createFilter (queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect (item) {
      console.log(item.value)
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    handleSizeChange (size) {
      this.pagesize = size
      this.getList()
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList () {
      let d = {
        tag: 'ad_config',
        op: 'get'
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>



